.CartScreen .content {
    display: flex;
    justify-content: space-between;
    gap: 15px;
}

@media(max-width:500px) {
    .CartScreen .content {
        flex-direction: column;
        justify-content: center;
    }
}

.CartScreen .left {
    flex-grow: 1.5;
}

.CartScreen .content .right {
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: white;
    padding: 10px;
    height: fit-content;
    box-shadow: 0 0 10px #DDDD;
    flex-grow: 1.1;
}

.go-back {
    color: white;
    width: 120px;
    padding: 10px 7px;
    background-color: rgb(32, 32, 32);
    cursor: pointer;
    margin: 20px 0;
    display: block;
    text-align: center;
    font-size: 17px;
    transition: 0.3s;
}

.go-back:hover {
    background-color: #242424dd;
}