.side-drower {
    position: fixed;
    left: 0;
    top: 0;
    width: 70%;
    height: 100vh;
    background-color: #FFF;
    z-index: 20;
    transform: translateX(-100%);
    transition: All 0.3s ease-out;
}

.side-drower.show {
    transform: translateX(0);
}

.side-drower .side-ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 10px 10px 0;

}

.side-drower .side-ul li {
    margin-bottom: 10px;
    background-color: #EEE;

}

.side-drower .side-ul li a {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px;
    color: black;
    transition: 0.3s;

}

.side-drower .side-ul li a:hover {
    background-color: black;
    color: white;
}


@media(min-width:768px) {
    .side-drower {
        display: none;
    }
}