.dropdown {
    position: absolute;
    bottom: -380px;
    left: -144px;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease;
    width: 200px;
    background-color: #ffffff;
    padding: 5px;
    border: 1px solid #b6b6b6dd;
    z-index: 12;
}


.dropdown div {
    padding: 10px;
    cursor: pointer;
    margin-top: 4px;
    transition: 0.3s ease-out;
    background-color: #ffffff;

}

.dropdown .user-info {
    display: flex;
    margin-top: 0;
    gap: 10px;
    padding: 10px;
    background-color: #f1f1f1;
    cursor: pointer;
}

.dropdown .logout {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.dropdown .user-message {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.dropdown .user-message span {
    width: 20px;
    height: 20px;
    background-color: #9306f1;
    color: white;
    text-align: center;
    border-radius: 50%;
}



.dropdown div:not(:last-of-type) {
    border-bottom: 1px solid #DDDD;
}

.dropdown div:hover {
    background-color: #9306f1;
    color: white;
}

.dropdown div:hover span {
    background-color: white;
    color: black;
}