form {
    position: absolute;
    z-index: 20;
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 40%;
    background-color: #EEE;
    padding: 19px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;

}

@media(max-width:768px) {
    form {
        width: 92%;
    }
}

form input {
    border: 0;
    padding: 10px 15px;
    background-color: #FFF;
    border-radius: 7px;
    font-size: 17px;

}

form input:focus {
    outline: 1px solid #0d6dd7;
}