.FavScreen .content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
}

.fav-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 15px;
    background-color: #FFF;
    box-shadow: 0 0 10px #DDD;
    padding: 10px;
}

.trash-icon {
    display: block;
    width: 20px;
    margin-left: auto;
    font-size: 20px;
    cursor: pointer;
    color: rgb(204, 7, 165);
}

.trash-icon:hover {
    color: rgb(210, 13, 13);
}

.FavScreen select {
    padding: 8px;
    font-size: 16px;
    display: block;
    background-color: white;
    border: 1px solid #DDD;
    outline: 0;
}

