.product {
    padding: 10px;
    background-color: white;
    box-shadow: 0 0 10px #c8c6c6;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 15px;
}


.product .img-container {
    height: 190px;
    overflow: hidden;
}

.img-container img {
    transition: 0.3s;
}

.product .product-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 15px;
}

.product .product-info p {
    color: rgb(82, 82, 82);
    line-height: 1.4;
}

/* .product .product-info h3 {
    margin: 10px 0;
} */

.product .product-info h4 {}

.product .product-info span {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: bold;
}

.product a {
    display: block;
    padding: 10px;
    color: black;
    border: 1px solid #DDD;
    background-color: #EEE;
    text-align: center;
    transition: 0.3s;
}

.product a:hover {
    background-color: black;
    color: white;
}

.heart-icon {
    cursor: pointer;
    font-size: 25px;
    color: rgb(3, 100, 211);
    transition: 0.3s;
}

.heart-icon:hover {
    color: rgb(7, 14, 220);
}