.ProductScreen {
    /* display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr)); */
    margin-top: 20px;
    gap: 28px;
    display: flex;
    justify-content: space-between;
}

@media(max-width:950px) {
    .ProductScreen {
        flex-direction: column;
    }
}


.ProductScreen>div {
    padding: 10px;
    background-color: #fff;
    box-shadow: 0 0 10px #DDD;
}

.ProductScreen .img-container {
    flex: 1.5;
}

.ProductScreen .info {
    flex: 1.5;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.ProductScreen .info h2 {
    border-bottom: 1px solid #DDD;
    margin: 10px;
    padding: 10px 0;
    margin-top: 0;
    padding-top: 0;
}

.ProductScreen .info p {
    border-bottom: 1px solid #DDD;
    margin: 10px;
    margin-top: 0;
    padding: 10px 0;
    padding-top: 0;
}

.ProductScreen .info p:nth-child(2) span {
    font-weight: bold;
}

.ProductScreen .right {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
}

.ProductScreen .right div {
    border-bottom: 1px solid #DDD;
    padding: 10px 0;
    padding-top: 0;
    margin: 10px;
    margin-top: 0;
    display: flex;
    justify-content: space-between;
}

.ProductScreen .right div span {
    font-weight: bold;
}

.ProductScreen .right select {
    width: 50px;
    font-size: 18px;
    border: 1px solid #DDD;
    outline: 1px solid #DDD;
    /* padding: 10px 0; */
    display: inline-block;
}