body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f0f0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
  object-fit: cover;
  height: 100%;
}

a {
  text-decoration: none;
}

.container {
  width: 80%;
  margin: auto;
}

@media(max-width:950px) {
  .container {
    width: 85%;
  }
}

@media(max-width:768px) {
  .container {
    width: 97%;
  }
}

button {
  border: 0;
  padding: 8px 10px;
  color: rgb(230, 228, 228);
  outline: 0;
  cursor: pointer;
  transition: 0.3s ease;
  background-color: #0d6dd7;
  font-size: 19px;
}

.container button:hover {
  background-color: #055bb7;

}

div.item {
  padding: 15px;
  transition: 0.2s;
  margin: auto;
  overflow: hidden;
  border-radius: 10px;
  transition: All 0.3s;
}

.image {
  overflow: hidden;
  border-radius: 10px;
  cursor: pointer;
}

.image:hover img {
  transform: scale(1.4);
}

.image img {
  transition: 0.3s ease-out;
  width: 100%;
  padding: 10px;
  background-color: white;
  height: 140px;
  object-fit: cover;
}

.btns {
  display: flex;
  align-items: center;
}

.btns button {
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
  margin: 0 5px;
  color: rgb(229, 229, 229);
  position: absolute;
  z-index: 10;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(0, 152, 228);
  top: 240px
}

.btns button:hover {
  background-color: #024acf;
}

.btns button:nth-child(1) {
  left: 130px;
}

.btns button:nth-child(2) {
  right: 130px;

}

@media(max-width:1200px) {
  .btns button:nth-child(1) {
    left: 40px;
    top: 240px
  }

  .btns button:nth-child(2) {
    right: 40px;
    top: 240px
  }
}

@media(max-width:768px) {
  .btns button:nth-child(1) {
    left: 20px;
    top: 237px
  }

  .btns button:nth-child(2) {
    right: 20px;
    top: 237px
  }

}