.CheckoutScreen {}

.CheckoutScreen .main {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    align-items: flex-start;
}

@media(max-width:768px) {
    .CheckoutScreen .main {
        flex-direction: column;
    }

    .CheckoutScreen .main .left,
    .CheckoutScreen .main .right {
        width: 100%;
    }
}

.CheckoutScreen .main .left {
    flex: 1;
    background-color: #f2f2f2;
    padding: 10px;
    box-shadow: 0 0 10px #bcbcbcdd;
}

.CheckoutScreen .main .left h3 {
    margin: 10px 0;
}

.CheckoutScreen .main .left div.Country {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin: 20px 0;
}

.CheckoutScreen .main .left div span:nth-child(2) {
    color: gray;
}

.CheckoutScreen .main .left select {
    display: block;
    width: 100%;
    padding: 10px 15px;
    border: 0;
    outline: 1px solid #DDD;
    background-color: white;
}

.CheckoutScreen .main .left select option {
    padding: 10px;
    font-size: 16px;
    outline: 1px solid #DDD;
    border: 0;
}

.CheckoutScreen .main .left div.pay {}

.CheckoutScreen .main .left div.pay div {
    margin: 18px 0;
    display: flex;
    align-items: center;
    gap: 10px;
    background-color: white;
    box-shadow: 0 0 10px #DDDD;
    cursor: pointer;
}

.CheckoutScreen .main .left div.pay div label {
    flex: 2;
    cursor: pointer;
    height: 100%;
    padding: 5px;
}

.CheckoutScreen .main .right {
    flex: 1;
    background-color: white;
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    justify-content: space-between;
    box-shadow: 0 0 10px #b8b8b8dd;

}

.right div {
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.right div:nth-child(3) {
    padding-bottom: 21px;
    border-bottom: 1px solid #DDDD;
}

.right h2 {
    margin: 20px 0;

}

input[type='radio'] {
    width: 30px;
    height: 30px;
    appearance: none;
    border: 1px solid #b8b8b8;
    position: relative;
    margin: 5px;
}

input[type='radio']::after {
    position: absolute;
    content: '';
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 12px;
    height: 12px;
    background-color: rgb(201, 201, 201);
}

input[type='radio']:checked::after {
    background-color: rgb(8, 183, 2);
}