nav {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    align-items: center;
    border-bottom: 1px solid #b5b5b5;
    width: 100%;
    z-index: 50;
    height: 90px;

}


nav .navbar-links {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 10px;
}

nav .navbar-links li {
    position: relative;
}

nav .navbar-links li:hover::before {
    height: 100%;
}

nav .navbar-links li:hover::after {
    width: 100%;
}

nav .navbar-links li::before {
    position: absolute;
    content: '';
    right: 0;
    top: 0;
    height: 0%;
    background-color: #c3c3c3;
    width: 1px;
    transition: 0.3s;
    z-index: 1;
}

nav .navbar-links li::after {
    position: absolute;
    content: '';
    right: 0;
    top: 0;
    transition: 0.3s;
    width: 0%;
    background-color: #c3c3c3;
    height: 1px;
    z-index: 1;
}

nav .navbar-links li a::before {
    position: absolute;
    content: '';
    background-color: #c3c3c3;
    left: 0;
    bottom: 0;
    height: 0%;
    transition: 0.3s;
    width: 1px;
}

nav .navbar-links li a::after {
    position: absolute;
    content: '';
    background-color: #c3c3c3;
    left: 0;
    width: 0%;
    transition: 0.3s;
    bottom: 0;
    height: 1px;
}

nav .navbar-links li a {
    position: relative;
    transition: 0.3s;
    background-color: #ffffff;
    display: flex;
    transition: 0.3s;
    gap: 4px;
    align-items: center;
    padding: 8px 10px;
    color: rgb(18, 18, 18);
    box-shadow: 0 0 14px #DDDD;
}

nav .navbar-links li a:hover::before {
    height: 100%;
}

nav .navbar-links li a:hover::after {
    width: 100%;
}

.icon {
    transition: 0.3s;
    font-size: 18px;
}

nav .navbar-links li a:hover .icon {
    color: #9306f1;
}

nav .navbar-links li a .cart-badge {
    width: 20px;
    height: 20px;
    background-color: #414141;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 50%;
    color: rgb(255, 255, 255);
    font-size: 1.1rem;
}

div.bars-container {
    padding: 5px;
    cursor: pointer;
    display: none;
    align-items: center;
    justify-content: center;
}

.bars-container .bars {
    color: rgb(8, 7, 7);
    font-size: 20px;
}

@media(max-width:768px) {
    ul.navbar-links {
        display: none;
    }

    div.bars-container {
        display: flex;
    }
}

.profile {
    position: relative;
}

.profile:hover .dropdown {
    opacity: 1;
    bottom: -337px;
    visibility: visible;
}



.dropdown .user-info img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: 4px;
    background-color: #EEE;
}