.cart-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 15px;
    padding: 10px;
    background-color: white;
}

.cart-item .img-container {}

.cart-item .img-container img {
    width: 70px;
    height: 70px;
}

.cart-item h3 {}

.cart-item span {}

.cart-item select {
    font-size: 18px;
    width: 50px;
    outline: 0;
    border: 1px solid #DDDD;
    background-color: white;
    display: inline-block;
}

.cart-item .icon {
    color: rgba(255, 0, 0, 0.68);
    cursor: pointer;
    font-size: 22px;
    margin-right: 10px;
}

.cart-item .icon:hover {
    color: red;
}